* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body, html {
  background: #fff;
  justify-content: center;
  align-items: center;
  height: 100%;
  display: flex;
}

.stopwatch {
  justify-content: center;
  align-items: center;
  width: 81vmin;
  height: 81vmin;
  display: flex;
}

.stopwatch__outer-ring {
  background: #fff;
  border: 1vmin dotted #000;
  border-radius: 75%;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  display: flex;
}

.stopwatch__inner-ring {
  justify-content: center;
  align-items: center;
  display: flex;
}

.stopwatch__hand {
  transform-origin: bottom;
  transition: transform .5s ease-in-out;
  position: absolute;
  bottom: 50%;
}

.stopwatch__hand--minute {
  background: #0700ff;
  border-radius: 2px;
  width: 3vmin;
  height: 20vmin;
  animation: 3600s steps(60, end) infinite rotate;
}

.stopwatch__hand--second {
  background: #2c8000;
  border-radius: 2px;
  width: 2vmin;
  height: 38vmin;
  animation: 60s linear infinite rotate;
}

.stopwatch__center-circle {
  z-index: 10;
  background: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  position: absolute;
}

.stopwatch--speed-up .stopwatch__hand--minute {
  animation-duration: 600s;
}

.stopwatch--speed-up .stopwatch__hand--second {
  animation-duration: 10s;
}

@keyframes rotate {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}
